@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap");

h1,
h2,
h3,
h4,
h5 {
  font-family: "SanshiroDisplayBlack", sans-serif;
}

p,
button,
a,
.font-code {
  font-family: "Source Code Pro", monospace;
}

.beefont{
  font-family: "OPTIIgnite", sans-serif;
}

::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-track {
  background: #100f10;
}

::-webkit-scrollbar-thumb {
  background: #f9f5f1;
  border-radius: 0px;
}

::-webkit-scrollbar-thumb:hover {
  background: #f9f5f1;
}

.text-container {
  font-size: 2rem;
  font-weight: bold;
  color: #000;
  text-transform: uppercase;
  text-align: center;
  margin-top: 50px;
}

@keyframes typing {
  from {
    opacity: 0;
    transform: translateY(-50%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes image {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

.animate-spinner {
  animation: spin 20s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
